.doctor_listing_topSection {
  justify-content: center;
   background: #f5f6f8;
  padding: 20px 0 25px;
  display: flex;
  text-align: left;
  font-family: "IBM Plex Sans";
  .doctorListWrap {
    display: flex;
  }
  .doctor_listing_speciality_div {
    opacity: 1;
    background: white !important;
    width: 358px;
    height: 960px;
    border-radius: 8px;
    margin-right: 14px;
    padding: 25px 10px 25px 20px;
    .doctor_listing_main_heading {
      font-size: 25px;
      font-weight: bold;
      letter-spacing: 0.25px;
      color: #011d29;
      opacity: 1;
      margin-left: 14px;
      padding-right: 10px;
    }
  }

  .doctor_listing_scrolling {
    overflow-y: auto;
    overflow-x: hidden;
    height: 860px;
    width: 100%;
    padding-right: 8px;
    // height: fit-content;
    .loader {
      text-align: center !important;
      color: #208ce1;
      position: relative;
      top:65px;
    }
  }
  .doctor_listing_scrolling::-webkit-scrollbar {
    width: 4px;
    height: 8px;
    color: white;
    border-radius: 4px;
  }

  .doctor_listing_scrolling::-webkit-scrollbar-thumb {
    background-color: #e2e2e2;
  }
  .doctorListFilter {
     width: calc(100% - 368px);
    .NoDataFound {
      text-align: center;
      padding-top: 20px;
      padding-bottom: 10px;
      position: relative;
      top:15%;
      .text {
        color: #011d29;
        opacity: 0.55;
        font-size: 20px;
      }
    }
  }
}
:global {
  .doctor_ListFilter {
    transition: all 0.3s ease-in-out;
  }
  .modal-open { 
    .doctor_ListFilter {
      // width: 65% !important;
    }
  }
}
@media screen and (max-width: 991px) {
  .doctor_listing_topSection {
    .doctor_listing_speciality_div {
      display: none;
    }
    .doctorListWrap {
      flex-direction: column;
    }
    .doctor_listing_speciality_div {
      width: 100%;
      margin-bottom: 15px;
    }
    .doctor_listing_scrolling {
      padding-right: 0;
      overflow-y: auto;
      height: auto;
    }
    .doctorListFilter {
      width: 100%;
    }
  }
  :global {
    .doctor_ListFilter {
      transition: none;
    }
    .modal-open { 
      .doctor_ListFilter {
        width: 100% !important;
      }
    }
  }
}
@media screen and (max-width: 575px) {
  .doctor_listing_topSection {
    .doctor_listing_speciality_div {
      padding: 25px 5px;
    }
  }
}
